//import 'bootstrap/dist/css/bootstrap.min.css';
import React, { Component } from 'react';
import {Tooltip, OverlayTrigger} from 'react-bootstrap';
import { Button } from 'react-bootstrap';
import { WpLink, GeneralLink } from './URLutils';

//{"title":"dsfsdfsfsf","description":"dsfsddfsdfdfsfsf","started":"2018-01-01 00:00:00","ended":"2018-01-02 00:00:00","links":[],"projTypes":[],"themes":[],"wmProjects":[],"languages":[],"users":[]}

class Card extends Component {
    
  render() {
      const { data } = this.props;
    return (
<div className="col-md-4">
<div className="panel panel-default">
  <div className="panel-heading">
    <h3 className="panel-title">{data.title}</h3>
  </div>
  <div className="panel-body">
    {data.description}<br />
  {
      data.users.length>0 && <div>
        <i>Who should I contact? </i> 
        {
          data.users.map((item,key) => (<WpLink label={item} project={"meta"} query={"User:"+item}/>))
            //.join(",")
            .reduce((prev, curr) => [prev, ', ', curr])
        }
      </div>
  }
  {
      data.themes.length>0 && <div>
        <i>Themes: </i> 
        {
          data.themes.reduce((prev, curr) => [prev, ', ', curr])
        }
      </div>
  }
  {
      data.projTypes.length>0 && <div>
        <i>Project type: </i> 
        {
          data.projTypes.reduce((prev, curr) => [prev, ', ', curr])
        }
      </div>
  }
  {
      data.wmProjects.length>0 && <div>
        <i>Wikimedia projects: </i> 
        {
          data.wmProjects.reduce((prev, curr) => [prev, ', ', curr])
        }
      </div>
  }
  {
      data.languages.length>0 && <div>
        <i>Languages/communities/countries: </i> 
        {
          data.languages.reduce((prev, curr) => [prev, ', ', curr])
        }
      </div>
  }
  </div>
</div>
</div>
    );
  }
}
/*

  <div className="panel-footer">
  <a href={"/#/project/"+data.id} target="_blank">Get more info!</a>
  </div>
*/

export default Card;