//import 'bootstrap/dist/css/bootstrap.min.css';
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {Navbar, Nav, NavItem} from 'react-bootstrap';
import {LinkContainer, IndexLinkContainer} from 'react-router-bootstrap';

class UserInfo extends Component {
  render() {
    const {islogged, name} = this.props;

    if (islogged) {
      return <span>Hi, {name}!</span>
    } else {
      return null
    }
  }
}

class Login extends Component {
  render() {
    const {islogged} = this.props;

    if (islogged) {
      return <NavItem href="https://tools.wmflabs.org/edgars/index.php?action=logout" target="_parent">Logout</NavItem>
    } else {
      return <NavItem href="https://tools.wmflabs.org/edgars/index.php?action=authorize" target="_parent">Login</NavItem>
    }
  }
}

class Header extends Component {
  render() {
    const {isLogged, userName} = this.props;

    return (
      <Navbar>
            <Nav>
              <IndexLinkContainer to="/">
                <NavItem>CEE projects</NavItem>
              </IndexLinkContainer>
              <LinkContainer to="/new"><NavItem>Add project</NavItem></LinkContainer>
            </Nav>
            {/*
            <Nav pullRight>
              <NavItem><UserInfo islogged={isLogged} name={userName} /></NavItem>
              <Login islogged={isLogged} />
            </Nav>
            */}
      </Navbar>
    );
  }
}

export default Header;