//import 'bootstrap/dist/css/bootstrap.min.css';
import React, { Component } from 'react';
import Card from './Card';
import Equalizer  from 'react-equalizer';
import Select from 'react-select';
import apiEndpoint from '../config';

const Intro = () => (
    <div>
      <h2>Welcome</h2>
      Welcome to CEE project database! Feel free to explore existing projects and please add yours!
    </div>
)


class Main extends Component {
    constructor(props) {
      super(props);
      this.state = {
        dataHasLoaded: false,
        dataLoaderror: false,
        projectData: [],
        filterData: [],
        
        titleFilter: "",
        themesFilter: [],
        typeFilter: [],
        wpProjectFilter: [],
        languageFilter: [],
        userFilter: []
      };
      this.handleInput = this.handleInput.bind(this);
      this.handleThemeChange = this.handleChange.bind(this, 'themesFilter');
      this.handleTypeChange = this.handleChange.bind(this, 'typeFilter');
      this.handleWMProjectChange = this.handleChange.bind(this, 'wpProjectFilter');
      this.handleLanguageChange = this.handleChange.bind(this, 'languageFilter');
      this.handleUserChange = this.handleChange.bind(this, 'userFilter');
    }
    handleChange (key, event) {
      //console.log(key);
      //console.log(event);
      this.setState({ [key]: event.map(item => item.value) });
      //console.log(this.state[key]);
    }

    handleInput(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        
        this.setState({
          [name]: value
        });
    }

    componentDidMount() {
        //fetch("https://tools.wmflabs.org/edgars/estlat.php?act=main")
        fetch(apiEndpoint+"?act=main")
        .then(res => res.json())
        .then(
          (result) => {
            this.setState({
                dataHasLoaded: true,
                projectData: result['data'],
                filterData: result['choices']
            });
          },
          // Note: it's important to handle errors here
          // instead of a catch() block so that we don't swallow
          // exceptions from actual bugs in components.
          (error) => {
            this.setState({
                dataHasLoaded: true,
                dataLoaderror: error
            });
          }
        )
    }

  render() {
    if (this.state.dataLoaderror) {
      return <div className="container">Some error occured, please contact <a href="https://lv.wikipedia.org/wiki/Dalībnieka_diskusija:Edgars2007" target="_blank">Edgars2007</a></div>
    } else if (this.state.dataHasLoaded) {
        //let themeFilter = ('one'=='one') ? (project => project.themes.some(r=> [].includes(r))) : project;

        console.log(this.state.themesFilter.length === 0);
        
        return (
            <div className="container">
            <Intro />
            <div className="clear"></div>
            <h3>Filter</h3>
            <div className="row">
            <div className="col-md-4">
            <label>Title</label>
            <input type="text" name="titleFilter" className="form-control" placeholder="My cool GLAM project" value={this.state.titleFilter} onChange={this.handleInput} />
            </div>
            <div className="col-md-4">
            <label>Theme</label>
            <Select isMulti options={this.state.filterData.themes} onChange={this.handleThemeChange} />
            </div>
            <div className="col-md-4">
            <label>Project type</label>
            <Select isMulti options={this.state.filterData.projTypes} onChange={this.handleTypeChange} />
            </div>
            </div>
            <div className="row">
            <div className="col-md-4">
            <label>Wikimedia project</label>
            <Select isMulti options={this.state.filterData.wmProjects} onChange={this.handleWMProjectChange} />
            </div>
            <div className="col-md-4">
            <label>Languages/countries</label>
            <Select isMulti options={this.state.filterData.languages} onChange={this.handleLanguageChange} />
            </div>
            <div className="col-md-4">
            <label>Users</label>
            <Select isMulti options={this.state.filterData.users} onChange={this.handleUserChange} />
            </div>
            </div>
            <div className="clear"></div>
            <h3>Projects</h3>
            <Equalizer>
                {this.state.projectData
                    .filter(project => this.state.titleFilter === '' || project.title.toLowerCase().includes(this.state.titleFilter.toLowerCase()))
                    .filter(project => this.state.themesFilter.length === 0 || project.themes.some(r=> this.state.themesFilter.includes(r)))
                    .filter(project => this.state.typeFilter.length === 0 || project.projTypes.some(r=> this.state.typeFilter.includes(r)))
                    .filter(project => this.state.wpProjectFilter.length === 0 || project.wmProjects.some(r=> this.state.wpProjectFilter.includes(r)))
                    .filter(project => this.state.languageFilter.length === 0 || project.languages.some(r=> this.state.languageFilter.includes(r)))
                    .filter(project => this.state.userFilter.length === 0 || project.users.some(r=> this.state.userFilter.includes(r)))
                    //.filter(project => project.themes.some(r=> [].includes(r)))
                    //.filter(project => project.title.includes("EST"))
                    .map((item,key) => (<Card data={item} key={key}/>))}
            </Equalizer>
            </div>
        );
    } else {
      return <div className="container">Wait a moment, the data is loading</div>
    }
  }
}

export default Main;