//import 'bootstrap/dist/css/bootstrap.min.css';
import React, { Component } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import {Tooltip, OverlayTrigger} from 'react-bootstrap';
import $ from 'jquery';
import Select from 'react-select';
import Creatable from 'react-select/lib/Creatable';
import { Button } from 'react-bootstrap';

import apiEndpoint from '../config';

import 'bootstrap-daterangepicker/daterangepicker.css';
import DatetimeRangePicker from 'react-bootstrap-datetimerangepicker';
import moment from 'moment';

class New extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      description: "",
      links: "",
      projTypes: [],
      themes: [],
      wmProjects: [],
      languages: [],
      users: [],
      
      loadedTypes: [{value: 'contest' , label: 'contest'}],
      loadedThemes: [{value: 'education' , label: 'education'},{value: 'GLAM', label: 'GLAM'}],
      loadedWmProjects: [{value: 'Wikipedia' , label: 'Wikipedia'},{value: 'Commons', label: 'Commons'},{value: 'etc.', label: 'etc.'}],
      loadedLanguages: [],
      loadedUsers: [],

      hasUserBennLoggedIn: false,
      startDate: undefined,
      endDate: undefined,
      ranges: {
        'Today': [moment(), moment()],
        'Yesterday': [moment().subtract(1, 'days'), moment().subtract(1, 'days')],
        'Last 7 Days': [moment().subtract(6, 'days'), moment()],
        'Last 30 Days': [moment().subtract(29, 'days'), moment()],
        'This Month': [moment().startOf('month'), moment().endOf('month')],
        'Last Month': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')],
      },
    };
    this.handleInput = this.handleInput.bind(this);
    this.handleEventStart = this.handleEventStart.bind(this);
    this.handleEventEnd = this.handleEventEnd.bind(this);
    this.saveToDB = this.saveToDB.bind(this);
    
    this.deleteStart = this.deleteData.bind(this,'start');
    this.deleteEnd = this.deleteData.bind(this,'end');

    this.handleTypesChange = this.handleChange.bind(this, 'projTypes');
    this.handleThemesChange = this.handleChange.bind(this, 'themes');
    this.handlewmProjectsChange = this.handleChange.bind(this, 'wmProjects');
    this.handlelanguagesChange = this.handleChange.bind(this, 'languages');
    this.handleusersChange = this.handleChange.bind(this, 'users');
    
  }
  
  saveToDB(e) {
    e.preventDefault();
    //console.log(this.state);
    
    if (this.state.title === "") {
      toast.error("You have to fill at least title!", { autoClose: false });
    } else {
      //console.log('valid');
      const toSend = {
        'title':this.state.title,
        'description':this.state.description,
        'links':this.state.links,
        'projTypes':this.state.projTypes,
        'themes':this.state.themes,
        'wmProjects':this.state.wmProjects,
        'languages':this.state.languages,
        'users':this.state.users,
        'start':this.state.startDate && this.state.startDate.format('YYYY-MM-DD') || '',
        'end':this.state.endDate && this.state.endDate.format('YYYY-MM-DD') || ''
      };
      
      $.ajax({
        type: "post",
        url: apiEndpoint,
        dataType:"json",
        data: {'data':toSend,'act':'new_article'},
      })
	    .done(function (response) {
			  if(response.status === "success") {
          toast.success("Project succesfully saved!", { autoClose: 3500 });
        } else if(response.status === "error") {
          toast.error("Error: " +response.message, { autoClose: false });
        }
      })
	    .fail(function (XMLHttpRequest, textStatus, errorThrown) {
			  console.log("Status: " + textStatus);
			  console.log("Error: " + errorThrown);
        console.log(XMLHttpRequest);
        toast.error("Error, see console for original error", { autoClose: false });
      });
    }
    
    //{this.state.emailError ? <span style={{color: "red"}}>Please Enter valid email address</span> : ''}
  }

  handleInput(event) {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;
    
    this.setState({
      [name]: value
    });
  }
  
  deleteData (key, event) {
    if (key==='start') {
      this.setState({
        startDate: undefined
      });
    } else if (key==='end') {
      this.setState({
        endDate: undefined
      });
    }
  }
  
  handleEventStart(event, picker) {
    this.setState({
      startDate: picker.startDate
    });
  }

  handleEventEnd(event, picker) {
    this.setState({
      endDate: picker.startDate
    });
  }
  handleChange (key, event) {
    //console.log(key);
    //console.log(event);
    this.setState({ [key]: event.map(item => item.value) });
    //console.log(this.state[key]);
  }
  
  render() {
    const projectTypes = ['contest','education','GLAM'];//defaultā lang jāieliek state no localstorage
    const projTypes = [{value: 'contest' , label: 'contest'},{value: 'education', label: 'education'}];
    //projectTypes.map(item => { value: item; label: item });
    
    let start = this.state.startDate && this.state.startDate.format('YYYY-MM-DD') || '';
    let end = this.state.endDate && this.state.endDate.format('YYYY-MM-DD') || '';
    
    let buttonStyle = { width: '100%' };
    
    return (
      <div className="container">
        
        <form className="form-horizontal">
  <div className="form-group">
    <label htmlFor="inputTitle" className="col-sm-2 control-label">Project title<span style={{color: 'red',fontSize:'large'}}>*</span></label>
    <div className="col-sm-6">
      <input type="text" className="form-control" name="title" placeholder="My cool GLAM project" value={this.state.title} onChange={this.handleInput} />
    </div>
  </div>
  <div className="form-group">
    <label htmlFor="inputDescription" className="col-sm-2 control-label">Project description</label>
    <div className="col-sm-6">
      <textarea name="description" className="form-control" rows="3" placeholder="What can you tell about this project?" value={this.state.description} onChange={this.handleInput}></textarea>
    </div>
  </div>
  <div className="form-group">
    <label htmlFor="inputDescription" className="col-sm-2 control-label">Links about project</label>
    <div className="col-sm-6">
      <textarea name="links" className="form-control" rows="3" placeholder="One link per line
http://www.example.com
https://meta.wikimedia.org/wiki/Wikimedia_Central_and_Eastern_Europe" value={this.state.links} onChange={this.handleInput}></textarea>
    </div>
  </div>
  <div className="form-group">
    <label htmlFor="inputProjectType" className="col-sm-2 control-label">Project type</label>
    <div className="col-sm-6">
    <Creatable placeholder='select one of existing or start typing your option...' isMulti name="inputProjectType" options={this.state.loadedTypes} onChange={this.handleTypesChange} />
    </div>{/* placeholder='select one of existing or start typing your option'*/}
  </div>
  <div className="form-group">
    <label htmlFor="inputProjectType" className="col-sm-2 control-label">Theme</label>
    <div className="col-sm-6">
    <Creatable placeholder='select one of existing or start typing your option...' isMulti name="inputProjectType" options={this.state.loadedThemes} onChange={this.handleThemesChange} />
    </div>
  </div>
  <div className="form-group">
    <label htmlFor="inputProjectType" className="col-sm-2 control-label">Wikimedia projects (wp, wd, qoute etc.)</label>
    <div className="col-sm-6">
    <Creatable placeholder='select one of existing or start typing your option...' isMulti name="inputProjectType" options={this.state.loadedWmProjects} onChange={this.handlewmProjectsChange} />
    </div>
  </div>
  <div className="form-group">
    <label htmlFor="inputProjectType" className="col-sm-2 control-label">Languages/countries/communities</label>
    <div className="col-sm-6">
    <Creatable placeholder='select one of existing or start typing your option...' isMulti name="inputProjectType" options={this.state.loadedLanguages} onChange={this.handlelanguagesChange} />
    </div>
  </div>
  <div className="form-group">
    <label htmlFor="inputProjectType" className="col-sm-2 control-label">Users to contact</label>
    <div className="col-sm-6">
    {/*<TagsInput value={this.state.tags} onChange={this.handleTagsChange} />*/}
    
    <Creatable placeholder='start typing your option...' isMulti onChange={this.handleusersChange} options={this.state.loadedUsers} />
    </div>
  </div>
  <div className="form-group">
    <label htmlFor="inputProjectType" className="col-sm-2 control-label">Start</label>
    <div className="col-sm-6">
          <DatetimeRangePicker
            singleDatePicker
            drops="up"
            autoUpdateInput={false}
            autoApply={false}
            startDate={this.state.startDate}
            onEvent={this.handleEventStart}
          >
            <Button className="selected-date-range-btn" style={buttonStyle}>
              <div className="pull-left">
                <i className="fa fa-calendar"/>
                &nbsp;
                <span>
                  {start}
                </span>
              </div>
              <div className="pull-right">
                <i className="fa fa-angle-down"/>
              </div>
            </Button>
</DatetimeRangePicker>
    </div>
    <div className="col-sm-1">
    <button type="button" className="btn btn-danger" onClick={this.deleteStart}>X</button>
    </div>
  </div>
  <div className="form-group">
    <label htmlFor="inputProjectType" className="col-sm-2 control-label">End</label>
    <div className="col-sm-6">
          <DatetimeRangePicker
            singleDatePicker
            drops="up"
            autoUpdateInput={false}
            startDate={this.state.endDate}
            onEvent={this.handleEventEnd}
          >
            <Button className="selected-date-range-btn" style={buttonStyle}>
              <div className="pull-left">
                <i className="fa fa-calendar"/>
                &nbsp;
                <span>
                  {end}
                </span>
              </div>
              <div className="pull-right">
                <i className="fa fa-angle-down"/>
              </div>
            </Button>
</DatetimeRangePicker>
    </div>
    <div className="col-sm-1">
    <button type="button" className="btn btn-danger" onClick={this.deleteEnd}>X</button>
    </div>
  </div>
  <div className="form-group">
    <div className="col-sm-offset-2 col-sm-10">
      <button type="button" className="btn btn-primary" onClick={this.saveToDB}>Save!</button>
    </div>
  </div>
</form>
      </div>
    );
  }
}

export default New;